import { Component } from "react";
import { Nav, Navbar } from 'react-bootstrap'
import {HashRouter,NavLink} from "react-router-dom";
import "./Navbar.css"

class Tavbar extends Component {

  render() {

    
  return <HashRouter>
        <Navbar bg="light" expand="lg">
          <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Nav.Link >
                <NavLink  className="Nav_link"activeClassName="activeRoute"activeStyle={{ color: 'teal' }}to="/home">Home</NavLink>
              </Nav.Link>
              <Nav.Link >
                <NavLink className="Nav_link"activeClassName="activeRoute"activeStyle={{ color: 'teal' }} to="/map">Map</NavLink>
              </Nav.Link>
              <Nav.Link >
                <NavLink className="Nav_link"activeClassName="activeRoute"activeStyle={{ color: 'teal' }} to="/galerie">Galerie</NavLink>
              </Nav.Link>

            </Nav>
            
            
          </Navbar.Collapse>
        </Navbar>
        
      </HashRouter>
  }
}



export default Tavbar;