import { Component } from "react";
import './UserForms.css';
import ReactDOM from 'react-dom';

class UserForms extends Component {
   
    constructor(){
        super();
        this.affiche = "";
        this.state = {
            firstName : '',
            lastName : '',
            phone : '',
            email:''
        }
        
    }
    handleSubmit =()=>{
    ReactDOM.render(
        
          <Info state={this.state}/>,
       
        document.getElementById('one')
      );
    }
   
    
    inputChange =(event)=>{
         console.log(event.target.name);
         switch (event.target.name){
             case "firstName" : {this.setState({firstName : event.target.value});
             console.log(this.state);
             break;
             }
             case "lastName" : {this.setState({lastName : event.target.value});
             console.log(this.state);
             break;
             }
             case "phone" : {this.setState({phone : event.target.value});
             console.log(this.state);
             break;
             }
             case "email" : {this.setState({email : event.target.value});
             console.log(this.state);
             break;
             }
            default : break;
            } 
         /*this.setState({
             firstName : event.target.value
         });*/
         

    }
    render() {
        
        return ( <div >
                        <h2 class="h1 my-2 font-weight-light">Inscrivez-Vous!</h2>

            <form  >
                
                <input  className="form-control form-control-lg"type = "text" placeholder = "First name" name="firstName"onChange={this.inputChange} />
                <input  className="form-control form-control-lg"type = "text" placeholder = "Last name"name="lastName" onChange={this.inputChange}/>
                <input  className="form-control form-control-lg"type = "text" placeholder = "email@email.com" name="email"onChange={this.inputChange} />
                <input  className="form-control form-control-lg"type = "number" placeholder = "Phone number"name="phone" onChange={this.inputChange}/>
                <button className="button"type ="button" onClick={this.handleSubmit}  >Inscrire</button>


            </form>
            
                <div className="divInfo"id="one" >

                </div>
            </div>
        )
    }

}


function Info(props){
    
    
    return <div >
    <p> First name : {props.state.firstName}</p>
    <p> Last name : {props.state.lastName}</p>
    <p> Phone  : {props.state.phone}</p>
    <p> E-mail  : {props.state.email}</p>
</div>


}



export default UserForms;